import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			cateMaskState: 0, //分类面板展开状态
			headerPosition:"fixed",
			headerTop:"0px",
			loadingType: 'more', //加载更多状态
			filterIndex: 0, 
			cateId: 0, //已选三级分类id
			priceOrder: 0, //1 价格从低到高 2价格从高到低
			cateList: [],
			goodsList: [],
			folderName: "goods",
			productPath: '/pages/product/product'
		};
	},
	
	onLoad(options){
		const item = JSON.parse(decodeURIComponent(options.item));
		console.log(item)



		this.cateId = item.categoryId;
		this.loadCateList(item.parentId,item.categoryId);
		this.loadData('add');
		wx.setNavigationBarTitle({
			title: item.categoryName
		})
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh(){
		this.loadData('refresh');
	},
	//加载更多
	onReachBottom(){
		this.loadData('refresh');
	},
	methods: {
		//加载分类
		async loadCateList(fid, sid){
			this.$minApi.uniapp_post(this.$base_api.getCategory,{unitNo: this.$base_api.UNIT_NO,mallId: this.$base_api.MALL_ID}).then(res => {
				let list = res.data;
				let cateList =  list.filter(item=>item.categoryId == fid);
				cateList.forEach(item=>{
					let tempList = list.filter(val=>val.parentId == item.categoryId);
					item.child = tempList;
				})
				this.cateList = cateList
			});
		},
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(type, loading) {
			console.log(type+"啊哈哈哈")
			//没有更多直接返回
			if(type === 'add'){
				if(this.loadingType === 'nomore'){
					return;
				}
				this.loadingType = 'loading';
			}else{
				this.loadingType = 'more'
			}
			
			
			this.$minApi.uniapp_post(this.$base_api.getGoodsByCategory,{mallId: this.$base_api.MALL_ID, categoryId: this.cateId}).then(res => {
				let goodsList = [];
				let result = res.data.forEach(item =>{
					item.primaryPicUrl = this.$base_api.fileReview + '?mallId=' + this.$base_api.MALL_ID + '&folderName=' + this.folderName +
						'&fileName=' + item.primaryPicUrl;
						goodsList.push(item)
				})
				if(type === 'refresh'){
					this.goodsList = [];
				}
					//筛选，测试数据直接前端筛选了
				if(this.filterIndex === 1){
					goodsList.sort((a,b)=>b.sellVolume - a.sellVolume)
				}
				if(this.filterIndex === 2){
					goodsList.sort((a,b)=>{
						if(this.priceOrder == 1){
							return a.unitPrice - b.unitPrice;
						}
							return b.unitPrice - a.unitPrice;
						})
					}
					
				this.goodsList = this.goodsList.concat(goodsList);
					
				
				this.loadingType  = this.goodsList.length == goodsList.length ? 'nomore' : 'more';
				if(type === 'refresh'){
					if(loading == 1){
						uni.hideLoading()
					}else{
						uni.stopPullDownRefresh();
					}
				}
			})

			},
			//筛选点击
			tabClick(index){
				if(this.filterIndex === index && index !== 2){
					return;
				}
				this.filterIndex = index;
				if(index === 2){
					this.priceOrder = this.priceOrder === 1 ? 2: 1;
				}else{
					this.priceOrder = 0;
				}
				uni.pageScrollTo({
					duration: 300,
					scrollTop: 0
				})
				this.loadData('refresh', 1);
				uni.showLoading({
					title: '正在加载'
				})
			},
		//显示分类面板
		toggleCateMask(type){
			let timer = type === 'show' ? 10 : 300;
			let	state = type === 'show' ? 1 : 0;
			this.cateMaskState = 2;
			setTimeout(()=>{
				this.cateMaskState = state;
			}, timer)
		},
		//分类点击
		changeCate(item){
			this.cateId = item.categoryId;
			wx.setNavigationBarTitle({
				title: item.categoryName
			})
			this.toggleCateMask();
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//详情
		navToDetailPage(productPath,item){
			//测试数据没有写id，用title代替
			let id = item.title;
			uni.navigateTo({
				url: productPath + '?item=' + encodeURIComponent(JSON.stringify(item))
			})
		},
		stopPrevent(){}
	},
}
